export const getRate = (closing_price, opening_price) => {
    const __closing_price  = parseFloat(closing_price)
    const __opening_price = parseFloat(opening_price)        
    const __now_price = __closing_price - __opening_price
    const __rate = (__now_price / __opening_price) * 100
    return parseFloat(__rate.toFixed(2));
}

export function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

export function getCookie() {
  return  {
    coin: localStorage.getItem("coin")
  }  
}

export function setCookie(name, value) {
  localStorage.setItem(name, value)
}


export default {
    getRate,
    getRandomInt,
    getCookie,
    setCookie
}