async function getBithumbSise(coin, cache = false) {    
    try{
        const {status, data} = await axios.get(`https://api.bithumb.com/public/ticker/${coin}_KRW` + (cache ? '_' + new Date().getTime() : ''));
        if(status === 200) {
            return data.data
        }
        return []
    }catch(error){
        // console.error(error.message)        
    }
}

