
import React, {useEffect, useState, useRef} from 'react'
import axios, {cacheAdapterEnhancer} from 'axios'
import Speech from 'react-speech';
import styled from 'styled-components'
import useInterval from '../hook/useInterval'
import './coinPrice.css'
import {
    BsBrightnessHigh,
    BsMoon,
    BsFillPlayFill, BsFillStopFill} from 'react-icons/bs'
import {GiSpeaker, GiSpeakerOff} from 'react-icons/gi'
import {AiOutlinePoweroff} from 'react-icons/ai'

import ComUtil from '../util/comUtil'
import store from '../store';

const colorList = ['hotpink', '#69c1ff', '#a3ff69']

const StyledText = styled.div`
    transition: 0.5s;
    color: ${p => p.color};
`
function BitcoinPrice(){

    const [data, setData] = useState()
    const [color, setColor] = useState(0)
    // const [color, setColor] =>



    useEffect(() => {
        search()
    }, [])

    //5ì´ˆë§ˆë‹¤ ì‹œì„¸ë§Œ ì¡°íšŒí•˜ì—¬ ì„¸íŒ…
    useInterval(() => {        
        search()     
    }, 5000)

    const search = async () => {
        const res = await axios.get(`https://api.bithumb.com/public/ticker/BTC_KRW` + '_' + new Date().getTime());
        const newData = res.data.data        
        const rate = ComUtil.getRate(newData.closing_price, newData.opening_price)
        // setData({...newData, rate: rate})
        setData(prev =>{
            return {...newData, rate: rate}
        })
    }

    if(!data) return null

    return(
        <StyledText color={data.rate > 0 ? store.COLOR.PLUS : store.COLOR.MINUS}>
            BTC&nbsp;&nbsp;
            {parseFloat(data.closing_price).toLocaleString('ko-KR')}ì›
            &nbsp;&nbsp;
            <span style={{fontSize: 16}}>
                {data.rate > 0 && '+'}{data.rate}%
            </span>
        </StyledText>
    )
}
export default BitcoinPrice